<template>
  <div class="resetTelphone">
    <mt-header title="" class="toubu" fixed>
        <mt-button icon="back" @click.native="$router.back(-1)" slot="left"></mt-button>
    </mt-header>
    <br><br><br>
    <div class="t"><img src="../assets/images/shouji.png" alt=""></div>
    <div class="t"><p style="font-size: 18px"><b>{{telphone}}</b></p></div>
    <div style="text-align: center"><p></p></div>
    <div style="text-align: center"><p>,</p></div>
      <br><br><br><br>
    <mt-button @click.native="changeTel"  type="primary" style="margin: 20px auto; display: block; width: 90%;"></mt-button>
  </div>

</template>


<script>
  import { MessageBox } from 'mint-ui';
  export default {
    name : 'resetTelphone',
    data(){
      return{
        telphone:''
      }
    },
    created(){
      this.telphone=this.$store.getters.getStorage('info').telphone;
    },
    methods:{
      changeTel(){
        console.log('');
        this.verifyTel();
      },
      verifyTel(){
        MessageBox.prompt('').then(({ value, action }) => {
         // console.log('');
          //console.log(value);
          //console.log(action);
          this.$ajax({
            method:'get',
            url:'/api/users_info/verifyPass',
            params:{
              telphone:this.telphone,
              pass:value,
            }
          }).then(response=>{
           // console.log(response);
            if(response.status==201){
           //   console.log("");
              this.$router.push('/changeTelphone');//

            }
            if(response.status==204){
              console.log("");
              this.$message.error('');
            }
          }).catch(function (err) {
            console.log(err);
          });
        });
      }
    }
  }

</script>



<style scoped lang="scss">

  .resetTelphone{
    .toubu{
      height: 55px;
      background-color: #096dd9;
    }
  }
  .t{
    text-align: center;
    padding: 30px 0 0 0
  }


</style>







